body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


background-color: rgb(70, 70, 70);
text-align: center;

}

:root{
  --color-primary:rgb(70, 70, 70);
  --color-shadow-light:rgb(114, 111, 111);
  --color-shadow-dark:rgb(54 52 52);
  --color-text:rgb(218, 218, 218);
  --color-text2:rgb(199, 196, 196);
  --box-shadows:5px 5px 12px 4px var(--color-shadow-dark),
  -5px -5px 10px 2px var(--color-shadow-light);
  --color-hover-button: rgb(0, 255, 255);
  
  
  --box-shadows-button:5px 5px 10px 2px var(--color-shadow-dark),
  -5px -5px 7px  var(--color-shadow-light);



  
}

/* Genaral Setting */
.container{
  width: 100%;
  margin: 0 auto;
  text-align: center;

}
section{

  width: 100%;
  margin: 0 auto;
  text-align: center;
}
h1{
  color: var(--color-text);
  margin: .5rem;
  
}
h2,h3,h4,h5{
  color: var(--color-text2);
}

ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
li{
  padding: 1rem;
  display: grid;
}
a{
  text-decoration: none;
  color: rgb(255, 255, 255);
}
header{
  margin: 0 auto;
  width: 100%;
  text-align: center;
}

.btn{
  padding: .7rem 1rem;
  border: 1px solid var(--color-primary);
  box-shadow: var(--box-shadows-button);
  background-color: var(--color-primary);
  color: var(--color-text);
  border-radius: 6px;
  transition: 400ms all ease;

}

.btn:hover{
border-radius: 6px 6px 6px 30px ;
transition: 400ms all ease;

  
}
.btn-primary{
  padding: .7rem 1.1rem;
  border: 1px solid var(--color-primary);
  box-shadow: var(--box-shadows-button);
  background-color: var(--color-primary);
  border-radius: 6px;
  color: var(--color-text);
  transition: 400ms all ease;

}
.btn-primary:hover{
  border-radius: 6px  6px 6px 30px;
  transition: 400ms all ease;
  color: rgb(0, 0, 0);
}





code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
