.portfolio-card img{
    width: 90%;
  
    border-radius: 15px;
   margin: 1rem;
  

}
.portfolio-section{
    
}

.portfolio-section h1{
    padding: 1rem;
    
}
.portfolio-main{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(300px, auto);
    width: 70%;
    margin:  0 auto;
   
   


gap: 2.6rem;
row-gap: 3rem;

}
.portfolio-card{
    background-color: var(--color-primary);
    border-radius: 15px;
    box-shadow: var(--box-shadows);
    opacity: 90%;
    transition: 400ms all ease;
    
    
}
.portfolio-card h3{
    margin-top: 0;
    color: rgb(193 83 32);
    padding: .5rem;
}
.portfolio-card-bottton{
    margin: 1.6rem;
    display:grid;
    gap: .9rem;
    grid-template-columns: 1fr 1.3fr;
    opacity: 60%;
    transition: 400ms all ease;
    

}
a{
    transition: 500ms all ease;
}




.portfolio-card:hover{
    border-radius:15px 80px 15px 15px;
    transition: 300ms all ease;

}
a:hover{
    color: rgb(0, 238, 255);
    font-weight: 300;
}





/* Media Queris */

/* tab version************ */

@media (max-width:1024px) {
    
    .portfolio-main{
          
        grid-template-columns: repeat(2, 1fr);
         
        
        
    }
}

/* mobile version*********** */

@media (max-width:700px) {
    
    .portfolio-main{
          
        grid-template-columns: repeat(1, 1fr);
         
        
        
    }
}

